import React, { useState } from "react";
import styled from "styled-components";
import CloseIcon from "@mui/icons-material/Close";
import Logo from "../images/logo.svg";
import { selectCars } from "../features/car/carSlice";
import { useSelector } from "react-redux";

const Container = styled.div`
  min-height: 60px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 60px;
  a {
    img {
      width: 150px;
      height: 20px;
    }
  }
  @media screen and (max-width: 500px) {
    padding: 15px 30px;
  }
`;

const Menu = styled.div`
  margin-left: 10rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;

  a {
    font-weight: 600;
    /* text-transform: uppercase; */
    color: black;
    font-size: 1.15rem;
    padding: 10px;
    margin-right: 20px;
    border-radius: 15px;
    transition: all 0.2s ease-in-out;
    &:hover {
      background-color: rgba(0, 0, 0, 0.1);
    }
  }

  @media screen and (max-width: 1500px) {
    display: none;
  }
`;

const RightMenu = styled.div`
  display: flex;
  align-items: center;
  a {
    font-weight: 600;
    padding: 10px;
    border-radius: 15px;
    transition: all 0.2s ease-in-out;
    color: black;
    font-size: 1.15rem;
    &:not(:last-of-type) {
      margin-right: 20px;
    }
    &:hover {
      background-color: rgba(0, 0, 0, 0.1);
    }
  }
`;

const CustomMenu = styled.a`
  cursor: pointer;
`;

const BurgerNav = styled.div`
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: white;
  width: 380px;
  z-index: 100;
  list-style: none;
  padding: 50px;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  transform: ${(props) => (props.show ? "translateX(100%)" : "translateX(0)")};
  transition: transform 0.5s ease-in-out;
  @media screen and (max-width: 700px) {
    width: 300px;
    padding: 30px;
  }
  li {
    padding: 5px 0;
    a {
      font-weight: 600;
      border-radius: 15px;
      display: block;
      transition: all 0.2s ease-in-out;
      padding: 10px;
      font-size: 1.1rem;
      @media screen and (max-width: 700px) {
        font-size: 0.9rem;
      }

      &:hover {
        background-color: rgba(0, 0, 0, 0.1);
      }
    }
  }
`;

const CustomClose = styled(CloseIcon)`
  cursor: pointer;
`;

const CloseWrapper = styled.div`
  display: flex;
  justify-content: flex-end;
  margin-bottom: 30px;
`;

export default function Header() {
  const [burgerState, setBurgerStatus] = useState(true);
  const cars = useSelector(selectCars);
  const [windowState, setWindowState] = useState(false);
  const [windowState2, setWindowState2] = useState(false);
  window.addEventListener("resize", () => {
    if (window.innerWidth < 1500) {
      setWindowState(true);
    } else {
      setWindowState(false);
    }

    if (window.innerWidth < 700) {
      setWindowState2(true);
    } else {
      setWindowState2(false);
    }
  });
  return (
    <Container>
      <div className={burgerState ? "blur" : "blur  blur--active"}></div>
      <a href="#">
        <img src={Logo} alt="logo" />
      </a>
      <Menu>
        {cars &&
          cars.map((car, index) => (
            <a key={index} href="#">
              {car}
            </a>
          ))}
      </Menu>
      <RightMenu>
        <a href="#" className="inv">
          Shop
        </a>
        <a href="#" className="inv">
          Account
        </a>
        <CustomMenu
          onClick={() => {
            setBurgerStatus(false);
          }}
        >
          Menu
        </CustomMenu>
      </RightMenu>
      <BurgerNav show={burgerState}>
        <CloseWrapper>
          <CustomClose
            onClick={() => {
              setBurgerStatus(true);
            }}
          />
        </CloseWrapper>
        {cars &&
          windowState &&
          cars.map((car, index) => (
            <li>
              <a key={index} href="#">
                {car}
              </a>
            </li>
          ))}
        <li>
          <a href="#">Existing Inventory</a>
        </li>
        <li>
          <a href="#">Used Inventory</a>
        </li>
        <li>
          <a href="#">Trade-in</a>
        </li>
        <li>
          <a href="#">Test Drive</a>
        </li>
        <li>
          <a href="#">Insurance</a>
        </li>
        <li>
          <a href="#">Cybertruck</a>
        </li>
        <li>
          <a href="#">Roadster</a>
        </li>
        <li>
          <a href="#">Semi</a>
        </li>
        <li>
          <a href="#">Charging</a>
        </li>
        <li>
          <a href="#">Powerwall</a>
        </li>
        <li>
          <a href="#">Commercial Energy</a>
        </li>
        <li>
          <a href="#">Utilities</a>
        </li>
        <li>
          <a href="#">Charging</a>
        </li>
        <li>
          <a href="#">Find Us</a>
        </li>
        {windowState2 && (
          <li>
            <a href="#">Shop</a>
          </li>
        )}
        {windowState2 && (
          <li>
            <a href="#">Account</a>
          </li>
        )}
      </BurgerNav>
    </Container>
  );
}
